import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { FileUploadComponent } from './fileUploader/fileUpload.component';
import { SequenceComponent } from './sequence/sequence.component';
import { HomeComponent } from './home/home.component';
import { DevelopersComponent} from "./developers/developers.component";
import { MaterialsComponent } from './materials/materials.component';
import { AboutComponent } from './aboutMe/about.components';
import { PredictComponent } from './predict/predict.component';



const routes: Routes = [
  { path: '', component: HomeComponent },
  { path: 'fromfile', component: FileUploadComponent },
  { path: 'sequence', component: SequenceComponent },
  { path: 'about', component: AboutComponent },
  { path:'developers', component: DevelopersComponent},
  { path: 'prediction', component: PredictComponent },
  {path:'materials',component:MaterialsComponent}
  
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})

export class AppRoutingModule { }
